.extra-order {
  margin: 0;
  border: 0;
  padding-bottom: 18px;
  position: relative;
  margin-bottom: 23px;

  @include content-paddings();

  @include respond(tablet) {
    margin-bottom: 22px;
    padding: 0;
  }

  @include respond(desktop) {
    max-width: 1300px;
    margin: 0 auto 29px;
  }
}

.extra-order__title {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  margin-bottom: 34px;
  padding-right: 20px;
  background-color: $white-color;

  &::before {
    @include respond(tablet) {
      content: "";
      position: absolute;
      width: 30px;
      height: 3px;
      background-color: #67b737;
      left: 0;
      top: 22px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #67b737;
    right: 0;
    top: 14px;
    z-index: -1;
    left: 20px;

    @include respond(tablet) {
      top: 22px;
    }
  }

  @include respond(tablet) {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 58px;
    padding-left: 40px;
    padding-right: 39px;
  }
}

.extra-order__item {
  position: relative;
  padding-left: 45px;
  min-height: 30px;
  display: flex;
  align-items: center;
  margin-bottom: 12px;

  &:hover {
    .extra-order__label::before {
      border: 1px solid $border-hover-color;
    }
  }

  &:active {
    opacity: 0.4;
  }

  @include respond(tablet) {
    width: 45%;
    min-height: 40px;
    margin-bottom: 32px;
    padding-left: 58px;
  }

  @include respond(desktop) {
    width: 245px;
    min-height: 40px;
    display: flex;
    align-items: center;
  }
}

.extra-order__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @include respond(tablet) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 40px;
  }
}

.extra-order__label {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;

  @include respond(tablet) {
    font-size: 20px;
    line-height: 20px;
  }

  &::before {
    content: "";
    width: 30px;
    height: 30px;
    position: absolute;
    border: 1px solid $border-grey-color;
    top: 0;
    left: 0;
    transition: 0.6s;

    @include respond(tablet) {
      width: 40px;
      height: 40px;
    }
  }

  &::after {
    content: "";
    display: none;
    width: 10px;
    height: 19px;
    border-bottom: 1px solid #67b737;
    border-right: 1px solid #67b737;
    position: absolute;
    transform: rotate(43deg);
    left: 10px;
    top: 2px;

    @include respond(tablet) {
      transform: rotate(44deg);
      left: 14px;
      top: 6px;
    }
  }
}

.extra-order__input {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;

  &:checked + .extra-order__label::after {
    display: block;
  }
}
