.social {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  padding-top: 22px;
  padding-bottom: 21px;

  @include respond(tablet) {
    border: 0;
    width: 45.76%;
    padding-top: 8px;
  }
}

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social__item {
  margin: 0 18px;
  width: 21px;
  display: flex;
  justify-content: center;

  &--fb {
    justify-content: flex-start;
  }

  @include respond(tablet) {
    margin: 0 12px;
    width: 30px;
  }
}

.social__icon {
  fill: $grey-color-icons;
  display: block;

  &:hover {
    fill: $main-color-theme;
  }

  &:active {
    fill: $main-color-theme;
    opacity: 0.4;
  }

  &--vk {
    @include respond(tablet) {
      width: 30px;
      height: 16px;
    }
  }

  &--inst {
    @include respond(tablet) {
      width: 23px;
      height: 23px;
    }
  }

  &--fb {
    @include respond(tablet) {
      width: 14px;
      height: 28px;
    }
  }
}
