.title {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-image: url("../img/index-background-mobile@1x.jpg"), linear-gradient(#65a93c 305px, transparent 305px);
  min-width: 320px;
  background-repeat: no-repeat;
  color: $white-color;
  padding: 26px 20px 0;
  position: relative;
  margin-bottom: 22px;

  @include retina-image("../img/index-background-mobile@2x.jpg",(#65a93c 305px, transparent 305px));
  background-size: 320px, auto;
  background-position: 50% 0, 0 0;

  @include respond(tablet) {
    background-image: none;
    color: $black-color;
    text-align: left;
    padding: 0;
    padding-left: 30px;
    padding-right: 30px;
    min-height: 810px;
    margin-bottom: 0;
  }

  @include respond(desktop) {
    min-height: 508px;
    padding-top: 34px;
    max-width: 1300px;
    margin: 0 auto 80px;
  }
}

.title__header {
  margin: 0;
  text-align: center;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin-bottom: 25px;

  @include respond(tablet) {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    width: 505px;
    padding-left: 87px;
    margin-bottom: 45px;
  }

  @include respond(desktop) {
    width: 52.42%;
    padding-left: 90px;
  }
}

.title__text {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 29px;

  @include respond(tablet) {
    font-size: 20px;
    margin-bottom: 23px;
    padding-left: 87px;
  }

  @include respond(desktop) {
    padding-left: 90px;
  }
}

.title__image {
  display: block;
  margin: 0 auto 3px;

  @include respond(tablet) {
    width: 708px;
    z-index: -1;
  }

  @include respond(desktop) {
    margin-top: -282px;
    margin-left: 460px;
    width: 552px;
  }
}
